import { DARK_MODE } from '../Actions/type.js';

const initialState = {
    darkmode:false
}

export default function(state = initialState, action){
    switch(action.type){
        case DARK_MODE:
            console.log("Dark mode Reducer...")
            return {
                ...state,
                darkmode: action.payload
            }
        default:
            return state;
    }
}